

#navigation-dropdown {
    display: none;
}
@media screen and (max-width: 800px) {

    body.home #menu-container {
            min-height: 0;
        }

    
        #navigation-dropdown {
            display: block;
            margin: auto;
            background: none;
            border: none;
            .hermes();
            text-transform: uppercase;
            font-size: 2em;
            width: 100%;
            box-sizing: border-box;
            padding: 2% 5%;
            text-align: left;
            cursor: pointer;
            border-bottom: 2px solid @light-slate;
        }

        

        #menu-container {

            min-height: 0;

            display: block;
            height: auto;

            .menu-primary-container {
                display: block;
                max-height: 9999px;
                transition: max-height .2s ease;
            }

            


            
           
            &.closed {
                 .menu-primary-container {
                     max-height: 0;
                     overflow: hidden;  
                 }
        
            }
            


        }
    

}


#menu {
    z-index:5;
   
    margin:0;
    padding:0;
    
    margin:10px auto;
    position:relative;

    width: 100%;

    text-transform: uppercase;
    text-align: center;

    height: auto;
    


    li {
        display: inline-block;
        a {
           
            //margin:6px 0 46px 0;
            padding:0 20px;
            border-right:2px solid @red;
            text-decoration:none;
            color:#444343;
            font-size:20px;

            display: block;
        }

        @media screen and (max-width: 800px) {
            display: block;
            width: 100%;
            border-bottom: 2px solid @light-slate;

            

            a {
                border-right: none;
                padding:20px 20px;
                
                


            }

            &:last-of-type {
                a {
                    border-bottom: none;
                }
            }
        }
    }
    li:last-of-type {
        a {
            border-right:none !important;
        }
    }
    li.last {
        a {
            border-right:none;
        }
    }
}




ul#menu li a.selected, ul#menu li.current-menu-item a span, ul#menu li a:hover {
    color:#ee2e24 !important;
}
